import classes from "./CartItem.module.css";

function CartItem() {
  return (
    <div className={classes["cart-item"]}>
      <h3>Arabica</h3>
      <div className={classes.actions}>
          <label>Amount:</label>
          <input></input>
        <p>Price: $13.49</p>
        <div>
          <button>+</button>
          <button>-</button>
        </div>
      </div>
    </div>
  );
}

export default CartItem;
