import Modal from '../Common/Modal';
import Button from '../Common/Button';
import CartItem from './CartItem';

import classes from './Cart.module.css';

function Cart () {
    return <Modal classNameModal={classes.modal}>
        <div>
            <CartItem></CartItem>
            <p>Total amount: $13.49</p>
            <Button>Order</Button>
        </div>
    </Modal>
}

export default Cart;